import React from "react";
import "../App.css";
import { useSelector } from "react-redux";

const Error = () => {
  const errorCSS = useSelector((state) => state.assessment.present.errorCSS);

  return (
    <div style={errorCSS}>
      <h1>ERROR</h1>
      <p>
        If you are non-LANC staff seeing this error, please alert someone at
        Legal Aid of North Carolina that this tool needs a fix. Please disregard
        the rest of this page.
      </p>

      <p>
        For LANC Staff: This tool is designed to pull data for the assessment
        from LANC MLP's github repository. Three things must be true for this
        page to load:
      </p>
      <ol>
        <li>The data file must be located in the folder named "Data."</li>
        <li>
          The data file must be in valid JSON format. Our MLP Tree App, also
          hosted in our github, is designed to output valid JSON. Please do not
          edit the file outside of the MLP Tree App before uploading.{" "}
        </li>
        <li>The name of the file must be "MLPTreeData.JSON"</li>
      </ol>
      <br />
      <p>
        Please review the LANC github with these conditions in mind to fix the
        error and restore functionality in this tool.
      </p>
    </div>
  );
};

export default Error;
