import React from "react";
import "../App.css";

const Header = () => {
  return (
    <div style={{ width: "75%", margin: "auto", fontSize: "larger" }}>
      <br />
      <img
        alt="lanclogo"
        src="https://www.legalaidnc.org/sites/default/files/2020-07/lanc-logo-ogp_0.png"
        className="App-logo1"
      />
      <br />
      <br />
      <h1>
        <b>Medical-Legal Partnership</b>
      </h1>
      <h2>
        <b>Referral Assessment Tool</b>
      </h2>
      <br />
      <div style={{ textAlign: "justify" }}>
        <p>
          This assessment tool was developed by Legal Aid of North Carolina's
          (LANC) Medical-Legal Partnership Program (MLP) to help health
          professionals:
        </p>
        <ol style={{ width: "100%", margin: "auto" }}>
          <li>Understand when to make a referral,</li>
          <li>Learn what LANC can do to assist their patients, and</li>
          <li>
            Access helpful information and resources to provide their patients,
            even if a referral is not appropriate.
          </li>
        </ol>
        <br />
        <p>
          At the completion of the assessment, all results will be listed at the
          bottom of this page. You can export these results for your records.
        </p>
        <p>
          Do you have thoughts about this assessment tool? Please consider
          sharing your feedback with us{" "}
          <a href="https://forms.office.com/Pages/ResponsePage.aspx?id=0TWofYd6lUCJz3d-0I9d--BEFY5RmV1NlHbvq69LxF5UM0lGUVFVSVRGWUpGSFFJRTM0VkQ5WjY0MS4u">
            <b>here</b>
          </a>
          .
        </p>
        <p>
          MLP Staff: To see the current tree, click{" "}
          <a href="https://lancmlp.github.io/MLP-JSON-API/Data/MLPTreeData.json">
            <b>here</b>
          </a>
          .
        </p>
      </div>
    </div>
  );
};

export default Header;
