import React from "react";
import "../App.css";
import { useSelector } from "react-redux";
import { Spinner } from "react-bootstrap";

const Loading = () => {
  const loadingCSS = useSelector(
    (state) => state.assessment.present.loadingCSS
  );

  return (
    <div style={loadingCSS}>
      <div
        style={{
          alignContent: "center",
          justifyContent: "center",
          top: "50%",
          left: "50%",
        }}
      >
        <b>Loading Assessment Tool...</b>
        <br />
        <Spinner
          style={{ margin: "30px" }}
          animation="border"
          variant="primary"
        />
      </div>
    </div>
  );
};

export default Loading;
